import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Paper, Typography, Avatar, IconButton } from '@material-ui/core';
import { MailOutline, Phone, LinkedIn, GitHub } from '@material-ui/icons';
import avatar from '../Images/avatar.jpg';
import { aquamarine } from 'color-name';

const useStyles = makeStyles(theme => ({
    wrapIcon: {
      verticalAlign: 'middle',
      display: 'inline-flex', 
      marginTop: '8px', 
      color: '#585959'
     }, 
     sideBar: { 
        marginTop: '16px',
        marginLeft: '16px',
        marginRight: '12px',
        paddingTop: '40px', 
        paddingLeft: '16px', 
        paddingRight: '16px',
        paddingBottom: '36px', 
        minWidth: '380px', 
        maxWidth: '24%'
     }, 
     avatar: { 
         margin: 'auto', 
         width: '150px', 
         height: '150px',
         marginBottom: '40px'
     }, 
     sideIcon: { 
         marginRight: '8px', 
         color: '#585959'
     }, 
     mainIcon: { 
         marginTop: '8px',
         color: '#63c7e6',
     },
     bio: { 
         marginTop: '100px',
         marginLeft: '8px', 
         marginRight: '8px',
         padding: '24px', 
         backgroundColor: '#f2f3f5',
         borderRadius: '10px',
         fontStyle: 'italic'
     }, 
     creds: { 
         marginTop: '130px',
     }
  }));

const Sidebar = () => {  
    const classes = useStyles();
    return (
            <Paper elevation={6} className={classes.sideBar}>  
                <Avatar src={avatar} className={classes.avatar} />
                <Typography variant="h3">Andie Montoya</Typography> 
                <Typography variant="subtitle1" className={classes.wrapIcon}>
                    <MailOutline className={classes.sideIcon} /> andiemontoyeah@gmail.com
                </Typography>  <br/>
                <Typography variant="subtitle1" className={classes.wrapIcon}>  
                    <Phone className={classes.sideIcon} /> + 778-772-0785
                </Typography>  
                <br/>
                <IconButton href="http://www.linkedin.com/in/andie-montoya-963a99170" className={classes.mainIcon}> 
                    <LinkedIn />
                </IconButton>
                <IconButton href="http://github.com/andiemontoyeah" className={classes.mainIcon}> 
                    <GitHub />
                </IconButton>
                <div className={classes.bio}>  
                 <Typography variant="body1"> 
                    Hi there! I'm Andie. :) I'm a new graduate from the University of British Columbia's computer engineering program. 
                    I have 16 months of co-op/internship experience: 8 months of full-stack web development and 8 months of native Android development. 
                    I am mainly looking for full-time opportunities in either of those areas but am open to anything interesting. Current technical interests 
                    include infrastructure-as-code tools, cloud platforms, and functional programming patterns.
                    Current random interests include cooking, baking, and binging everything on Netflix!
                 </Typography> 
                 </div>
                <div className={classes.creds}> 
                    <p> Built with <a href="https://github.com/facebook/create-react-app">create-react-app</a> and <a href="https://material-ui.com/">material-ui</a>.</p>
                </div>
            </Paper> 
    ); 
}

export default Sidebar;